.movie {
    margin-top:70px;
    background-color: white;
    margin-bottom: 70px;
    font-weight: 300;
    padding: 20px;
    border-radius: 5px;
    color: #adaeb9;
    display: grid;
    display:flex;
    grid-template-columns: minmax(150px, 1fr) 2fr;
    grid-gap: 20px;
    text-decoration: none;
    color: inherit;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
      0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  }
  
.movie__img {
    position: relative;
    top: -50px;
    width:150px;
    margin-right: 30px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
      0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}
  
.movie__title,
.movie__year {
    margin: 0;
    font-weight: 300;
    text-decoration: none;
}
a {
    text-decoration: none;
    color:black;
}
.movie__title a {
    margin-bottom: 5px;
    font-size: 24px;
    color: #2c2c2c;
    text-decoration: none;
}
  
.movie__genres {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    margin: 5px 0px;
    padding-left:0px;
}
.movie__genres li,
.movie__year {
    
    margin-right: 10px;
    font-size: 14px;
    
}


body{
    background:linear-gradient(rgb(72, 37, 228),pink,rgb(248, 177, 46), rgb(243, 243, 131),rgb(156, 183, 233) );
    /*background:linear-gradient(rgb(156, 183, 233), rgb(115, 117, 241),rgb(53, 54, 110)); */
}